// base
@import './base/config';
@import './base/global';
@import './base/helpers';

// component
@import './components/header';
@import './components/story';
@import './components/oneliner';
@import './components/gallery';
@import './components/modal';
@import './components/contact';
@import './components/footer';

main {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 80vh;
	min-height: 550px;
	overflow: hidden;

	.bg-video-container {
		position: relative;
		right: 2%;
	}

	.headline-container {
		position: absolute;
		transform: translateY(-60%);
		color: $white;
	}
}

@media screen and (min-width: $lgdesktop) {
	.bg-video-container {
		width: 100%;
		transform: translateX(-26%);
	}
}
