.contact {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	.container {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		text-align: center;
		padding-bottom: 3%;

		h1 a {
			font-family: $display;
			font-size: 2rem;
			font-weight: 200;
			color: $black;
			text-decoration: none;

			&:hover {
				border-bottom: 2px solid $black;
				padding-bottom: 5px;
			}
		}
	}
}
