*,
*:before,
*:after {
	margin: 0;
	padding: 0;
}

body {
	background-color: $bg-body;
	font-size: 62.5%;
}

h1 {
	&.title {
		font-family: $display;
		font-size: 4.5rem;
		font-weight: 200;
		line-height: 1.1;
		text-transform: uppercase;
	}

	&.subtitle {
		font-family: $display;
		font-size: 1.5rem;
		font-weight: 200;
		line-height: 1.1;
	}
}

h4 {
	&.title {
		font-family: $display;
		font-weight: 700;
		font-size: 5.5rem;
		text-transform: uppercase;
		color: $black;
	}
}

p {
	font-size: 1.5rem;
	line-height: 1.25;
}
