section {
	padding: 40px 0;
}
section.full {
	height: 100vh;
	min-height: 600px;
}
section.half {
	height: 50vh;
	min-height: 300px;
}
.container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 20px;
}

.flex {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.flex-col {
	width: 100%;
}

@media screen and (min-width: 768px) {
	.flex-col {
		max-width: 49.9%;
	}
}
