.modal {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	min-height: 600px;
	z-index: 1;
	background-color: $modal-bg;

	.modal-container {
		width: 90%;
		max-width: 1080px;
		background-color: $modal-container-bg;
		padding: 20px;

		img {
			width: 100%;
			margin: 0 auto;
		}
	}
}
